import React from "react";
import "../Nav/nav.css";
import { Link } from "react-router-dom";

const Nav = ({
  login,
  setLogin,
  privateUser,
  setPrivateUser,
  setUser,
  user,
}) => {
  return (
    <div className="navbar">
      <nav className="fixed-top navbar navbar-expand-sm navbar-light bg-light">
        <div className="container">
          <div className="items">
            <ul className="navbar-nav">
              <Link to="/Home">
                <li className="nav-item">
                  <a className="nav-link active" href="/Home">
                    Home
                  </a>
                </li>
              </Link>
              <Link to="/About">
                <li className="nav-item">
                  <a className="nav-link active" href="/About">
                    About
                  </a>
                </li>
              </Link>
              {privateUser && (
                <Link to="/Gallary">
                  <li className="nav-item">
                    <a className="nav-link active" href="/Gallary">
                      Gallary
                    </a>
                  </li>
                </Link>
              )}
            </ul>
          </div>
          {privateUser && (
            <h3>
              Hey,{" "}
              <span style={{ color: "violet", fontWeight: "700" }}>
                {user}üòÉ
              </span>
            </h3>
          )}
          <Link to="/Login">
            <button
              type="button"
              className="btn btn-primary"
              onClick={() => {
                setLogin((prev) => !prev);
                setUser("");
                setPrivateUser(false);
              }}
            >
              {!login ? "Login" : "Logout"}
            </button>
          </Link>
        </div>
      </nav>
    </div>
  );
};

export default Nav;
